body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
  background: linear-gradient(to bottom, rgb(64, 169, 255), rgb(116, 141, 255));
  background-image: url("https://images.pexels.com/photos/37728/pexels-photo-37728.jpeg?w=1260&h=750&auto=compress&cs=tinysrgb");
  background-repeat: repeat-y;
  background-size: cover;
  height:100vh;
}

footer{
  height:24px;
  width: 100%;
  background-image: none;
  background-repeat: repeat;
  background-attachment: scroll;
  background-position: 0% 0%;
  position: fixed;
  bottom: 0pt;
  left: 0pt;
  opacity: .6;
}

.footer-contents {
  height:24px;
  width: 100%;
  margin:auto;
}

.project-image {
  max-width:100%;
  height:400px;
  padding-left: 5%;
  padding-right: 5%;
  padding-bottom: 5%;
}

.project-info {
  padding-left: 15%;
  padding-right: 15%;
}

.project-div {
  font-family: Georgia;
}

#navbar {
   font-size: 22px;
}

#about-me-btn {
    background-color: transparent;
    border: none;
    cursor: pointer;
    color: rgba(59, 59, 75, 0.685);
}

#about-me-btn:hover {
    background-color: transparent;
    border: none;
    cursor: pointer;
    color: rgba(27, 27, 36, 0.822);
}

.nav-item {
    box-shadow: .5px .5px 1px black;
    background-color: rgba(54, 54, 54, 0.055)
}

.nav-item:hover {
    background-color: rgba(54, 54, 54, 0.123)
}

.dropdown-menu.show {
    background-color: rgba(54, 54, 54, 0.055)
}

button.dropdown-item:hover {
    background-color: rgba(54, 54, 54, 0.123);
    color: rgba(27, 27, 36, 0.822);
}

button.dropdown-item {
    color: rgba(59, 59, 75, 0.685);
}

.dropdown-menu.show {
    text-align: center
}

.ml-auto {
    margin-right: 50px;
}


#about-me-pic {
    height: 200px;
    padding-bottom: 35px;
    border-radius: 80px 80px;
}

#about-me-div {
    font-family: serif;
    padding-left: 15%;
    padding-right: 15%;
}

#about-me-header {
    padding-bottom: 40px
}

#about-me-txt {
    font-size: 20px;
    font-style: oblique;
    text-shadow: .5px .5px grey;
}






